<template>
  <div class="home-layout">
    <div class="h-head">
      <img @click="clearData" :src="img.back"/>
      <span></span>
    </div>
    <div class="h-body" id="hBody">
      <!--      <div style="margin-top: 15px">-->
      <!--        <img :src="img.left" class="userImg"/>-->
      <!--        -->
      <!--      </div>-->
      <div
          :class="item.sign === 'vioce' || item.sign === 'msg' ? 'right-msg' : ''"
          v-for="(item, index) of list"
          :key="index + 'ds'"
      >
        <div
            v-if="item.time"
            class="time"
        >
          {{ item.time }}
        </div>
        <img v-if="item.sign == null || item.sign === 'first'" :src="img.left" class="userImg"/>
        <div class="txt1" v-if="item.sign === 'first'">
          <div>
            hi
          </div>
          <div class="block-qus">
            <div v-for="(list, index) of serveList" :key="index + 'po'">
              <div @click="answerRes(index)">
                <img :src="require('../assets/' + list.img + '.png')"/>
                <div>{{ list.name }}</div>
              </div>
            </div>
          </div>
        </div>
        <div
            @click="playVioce(item.id, item.sign)"
            v-if="item.sign !== 'first'"
            :class="item.sign == null ? 'txt1' : 'txt'"
        >
          <!-- <span v-if="item.sign == null">{{ item.msg }}</span> -->
          <!-- signviocemsg -->
          <span v-if="item && item.sign == 'vioce'"
          ><i class="el-icon-video-play"></i>&nbsp;"{{ item.lengths }}</span
          >
          <span v-if="item.sign == 'msg' || item.qes === true">{{
              item.msg
            }}</span>
          <div
              class="qus-one"
              v-if="item.sign == null && item.list && item.list.length > 0"
          >
            <div></div>
            <div @click="changeSort(index, item.lodMsg, item.list)">
              <img :src="require('../assets/refresh.png')"/>
              {{ item.oldMsg }}
            </div>
          </div>
          <div
              v-if="item.sign == null && item.list && i < 3"
              class="qus-two"
              v-for="(list, i) of item.list"
              :key="i + 'qus'"
              @click="answerDetailRes(list.name, item.oldIndex)"
          >
            <div>{{ list.name }}</div>
            <img :src="require('../assets/right.png')"/>
          </div>
          <div
              v-if="item.sign == null && item.list && item.list.length > 0"
              class="qus-there"
          >
            <div>
              <img @click="comments(true, index)" :src="item.up?goodImg.up1:goodImg.up"/>
              <div></div>
            </div>
            <div>
              <img @click="comments(false, index)"
                   :src="item.up?goodImg.down:item.up === null?goodImg.down:goodImg.down1"/>
              <div></div>
            </div>
          </div>
        </div>
        <img v-if="item.sign != null && item.sign !== 'first'" :src="img.right" class="userImg"/>
        <div
            v-if="item.sign == 'vioce' && item.msg && item.msg.length > 0"
            class="fanyi"
        >
          <div>{{ item.msg }}</div>
        </div>
      </div>
    </div>

    <div class="h-foot">
      <img
          @click="keyState = !keyState"
          :src="keyState ? img.vioce : img.keys"
          style="width: 2rem"
      />
      <div class="btn-parent" v-show="!keyState">
        <el-button v-swipeup="cancelSend" @touchstart.native="enter" @touchend.native="leave"
        ><strong>{{ loading ? " " : " " }}</strong></el-button
        >
      </div>
      <el-input
          v-show="keyState"
          placeholder=""
          v-model="msg"
          @keyup.enter.native="sendMsg"
      ></el-input>
      <img :src="img.send" @click="sendMsg"/>
    </div>
    <!--  -->
    <div v-if="loading"  class="vioce-model">
      <img :src="vioceImgList[gifImg]"/>
      <div></div>
    </div>
    <!--  -->
    <div class="mind-model" v-if="mindView">
      <div
          @click="mindSend(list.value)"
          v-for="(list, index) of mindList"
          :key="index + 'opl'"
      >
        {{ list.list[0] }}<span class="color2">{{ list.list[1] }}</span
      >{{ list.list[2] }}
      </div>
    </div>
  </div>
</template>

<script>
import question from "../mock/index";
import {baseUrl} from "../util/index";
import axios from "axios";
import moment from "moment";

export default {
  data() {
    return {
      img: {
        back: require("../assets/back.png"),
        left: require("../assets/userLeft.png"),
        right: require("../assets/userRight.png"),
        send: require("../assets/send.png"),
        vioce: require("../assets/vioce.png"),
        keys: require("../assets/keys.svg"),
      },
      isClear: false,
      goodImg: {
        down: require('../assets/goodDown.png'),
        down1: require('../assets/isGoodDown.png'),
        up: require('../assets/goodUp.png'),
        up1: require('../assets/isGoodUp.png')
      },
      msg: "",
      mindList: [],
      vioceImgList: [],
      serveList: [
        {name: "", img: "s1"},
        {name: "", img: "s3"},
        {name: "", img: "s4"},
        {name: "", img: "s6"},
        {name: "", img: "s2"},
        {name: "", img: "s5"},
      ],
      gifImg: 0,
      vioceTime: 0,
      loadingChange: false,
      loadingOther: false,
      timer: null,
      loading: false,
      isRecord: true,
      mindView: false,
      keyState: true,
      token: "",
      list: [],
      id: "",
    };
  },
  watch: {
    msg(val) {
      if (this.msg.length === 0) {
        this.mindView = false;
        return;
      }
      this.mindList = [];
      let url = baseUrl + "/WxService/DataExchange?query=";
      url += this.msg;
      axios
          .get(url)
          .then((res) => {
            this.mindView = true;
            if (res && res.data && res.data.result) {
              let list = res.data.result;
              list.forEach((e) => {
                let index = e.question.indexOf(this.msg);
                this.mindList.push({
                  value: e.question,
                  list: [
                    index === 0 ? "" : e.question.substring(0, index),
                    this.msg,
                    index === e.question.length
                        ? ""
                        : e.question.substring(
                        index + this.msg.length,
                        e.question.length
                        ),
                  ],
                });
              });
            } else {
              this.mindView = false;
            }
          })
          .catch((err) => {
            console.log(err);
          });
    },
  },
  methods: {
    //
    cancelSend() {
      this.loadingChange = true;
    },
    //
    clearData() {
      this.isClear = true;
      localStorage.removeItem('aiHistory')
    },
    //
    answerRes(index) {
      if (this.loadingOther) return;
      if (index === 5) {
        let url = "https://www.wjx.cn/m/100773984.aspx";
        window.open(url);
        return;
      }
      let name = "";
      name = this.serveList[index].name;
      if (index === 4) {
        name = "";
      }
      this.answerDetailRes(name, undefined, "big");
    },
    //
    answerDetailRes(name, index, sign) {
      if (this.loadingOther) return;
      if (name && name.length > 800) name = name.substring(0, 800);
      if (index == undefined) index = 0;
      if (sign !== "vioce") {
        this.list.push({
          sign: "msg",
          msg: name,
          year: this.getTime('year'),
          time: this.getTime()
        });
      }
      this.getRes(name, index, sign);
      this.setFoot();
    },
    //
    mindSend(data) {
      this.mindView = false;
      this.msg = "";
      let index = this.getArrIndex(data);
      this.answerDetailRes(data, index);
    },
    //
    comments(sign, index) {
      this.list[index].up = sign;
    },
    //
    getRes(msg, index, sign) {
      let url = baseUrl + "/WxService/record?query=" + msg;
      this.loadingOther = true;
      axios
          .get(url)
          .then((res) => {
            this.loadingOther = false;
            if (res) {
              let obj = {};
              obj.lodMsg = msg;
              obj.list = [];
              if (
                  res.data.dm &&
                  res.data.dm.inspire &&
                  res.data.dm.inspire.length > 0
              ) {
                res.data.dm.inspire.forEach((e) => {
                  obj.list.push({
                    name: e.widget.text,
                  });
                });
              }
              if (sign !== "big") obj.msg = res.data.dm.nlg;
              if (obj.msg && obj.msg.length > 800)
                obj.msg = obj.msg.substring(0, 800);
              obj.oldIndex = index;
              obj.qes = true;
              obj.up = null;
              obj.year = this.getTime('year');
              obj.time0 = this.getTime(); //
              //
              console.log(obj, "'=???");
              this.list.push(obj);
              this.setFoot();
            }
          })
          .catch((err) => {
            this.loadingOther = false;
            console.log(err);
          });
    },
    getTime(sign) {
      let time = new Date();
      if (sign === 'year') return moment(time.getTime()).format('YY');
      return moment(time.getTime()).format('MM-DD HH:mm');
    },
    //
    changeSort(index, name, data) {
      let url =
          baseUrl + "/WxService/DataExchange/getAllQuestion?query=" + name;
      axios
          .get(url)
          .then((res) => {
            if (res.data && res.data.result && res.data.result.length > 0) {
              let list = res.data.result;
              for (let i = 0; i < data.length; i++) {
                for (let j = 0; j < list.length; j++) {
                  if (list[j].question === data[i].name) {
                    list.splice(j, 1);
                    j = j - 1;
                  }
                }
              }
              if (list.length >= 3) {
                let sum = [];
                sum[0] = Math.floor(Math.random() * list.length);
                let p = true;
                setTimeout(() => {
                  p = false;
                }, 6000);
                while (p) {
                  sum[1] = Math.floor(Math.random() * list.length);
                  sum[2] = Math.floor(Math.random() * list.length);
                  if (sum[1] !== sum[0] && sum[2] !== sum[0] && sum[2] !== sum[1])
                    break;
                }
                console.log(data, list, sum);
                this.list[index].list = [];
                sum.forEach((e) => {
                  this.list[index].list.push({
                    name: list[e].question,
                  });
                });
                this.$forceUpdate();
                console.log(this.list[index]);
              } else {
                this.$message.warning("");
              }
            } else {
              this.$message.warning("");
            }
          })
          .catch((err) => {
            console.log(err);
          });
    },
    //gif
    gifImgChange() {
      if (this.timer) clearInterval(this.timer);
      this.vioceTime = 0;
      this.timer = setInterval(() => {
        this.vioceTime++;
        if (this.gifImg > 6) this.gifImg = 1;
        else this.gifImg++;
      }, 100);
    },
    enter(e) {
      e.preventDefault();
      // this.loading = true;
      this.start();
    },
    leave() {
      // this.loading = false;
      console.log(this.loadingChange);
      setTimeout(() => {
        console.log(this.loadingChange, 'lll');
        this.clearGif();
        this.stop();
      }, 300);
    },
    clearGif() {
      if (this.timer) clearInterval(this.timer);
    },
    //
    changeIn() {
      this.keyState = !this.keyState;
    },
    //||
    sendMsg(data, res) {
      console.log(res);
      if (res != null) {
        let index = this.getArrIndex(res);
        this.answerDetailRes(res, index, "vioce");
        return;
      }
      if (this.msg.length === 0) {
        this.$message.warning("");
        return;
      }
      let index = this.getArrIndex(this.msg);
      this.answerDetailRes(this.msg, index);
      this.msg = "";
    },
    //
    getArrIndex(str) {
      let obj = JSON.parse(JSON.stringify(question));
      let sum = undefined;
      obj.forEach((e, index) => {
        e.list.forEach((es) => {
          if (es.name.indexOf(str) !== -1) {
            sum = index;
          }
        });
      });
      return sum;
    },
    //
    start() {
      let that = this;
      this.$wx.startRecord({
        success: function (e) {
          // 
          that.loading = true;
          that.gifImgChange();
          console.log(e, "");
        },
        cancel: function (e) {
          console.log(e);
        },
      });
    },
    //
    playVioce(id, sign) {
      console.log(id, "");
      if (sign === "vioce") this.$wx.playVoice({localId: id});
    },
    //
    setFoot() {
      this.$nextTick(() => {
        let div = document.getElementById("hBody");
        div.scrollTop = div.scrollHeight;
      });
    },
    //
    cancelVioce() {
      console.log("");
      this.isRecord = false;
    },
    //
    stop() {
      let that = this;
      this.$wx.stopRecord({
        success: function (e) {
          that.loading = false;
          let length = 0;
          if (that.vioceTime > 0) length = parseInt(that.vioceTime / 10);
          if (length === 0) length++;
          if (!that.loadingChange) {
            that.$wx.translateVoice({
              localId: e.localId, // Id
              isShowProgressTips: 1, // 1
              success: function (res) {
                if (that.isRecord) {
                  that.list.push({
                    sign: "vioce",
                    id: e.localId,
                    time: that.getTime(),
                    lengths: length,
                    msg: res.translateResult,
                  });
                  that.sendMsg("", res.translateResult);
                }
              },
            });
          } else {
            that.loadingChange = false;
          }
          that.isRecord = true;
          console.log(e, e.localId, "");
        },
        cancel: function (e) {
          that.loadingChange = false;
          console.log(e);
        },
      });
    },
  },
  created() {
    // this.getToken();
    let obj = JSON.parse(localStorage.getItem("aiHistory"));
    //
    let year = moment((new Date()).getTime()).format('YY');
    for (let i = 0; obj && i < obj.length; i++) {
      if (year !== obj[i].year) {
        obj.splice(i, 1);
        i--;
      }
    }
    console.log('1.2', obj);
    // 
    if (obj != null && obj.length > 0) {
      if (obj[0].sign !== 'first')
        this.list = [{
          sign: 'first',
          time0: this.getTime()
        }];
      this.list = this.list.concat(obj);
      //
      let time0 = this.list[this.list.length - 1].time0;
      let time1 = this.getTime();
      if (time0 && time0.length > 0) {
        let time = moment(time1).format('x') - moment(time0).format('x');
        let date = moment.duration(time).hours();
        if (date > 11 && this.list.length > 2) {
          this.list.push({
            sign: 'first',
            time0: this.getTime()
          });
        }
      }
    } else {
      this.list = [{
        sign: 'first'
      }]
    }
    for (let i = 1; i < 9; i++) {
      this.vioceImgList[i - 1] = require("../assets/vioce/" + i + ".png");
    }
  },

  mounted() {
    let div = document.getElementById("hBody");
    if (div) {
      div.scrollTop = div.scrollHeight;
    }
    //
    window.onbeforeunload = (e) => {
      if (!this.isClear)
        localStorage.setItem("aiHistory", JSON.stringify(this.list));
      // return "";
    };
  },
};
</script>

<style lang="scss">
@import "./aiHome.scss";
</style>